@import 'helper/bulma.css';
@keyframes movein {
  from {
    right: -520px;
  }
  to {
    right: 10px;
  }
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes moveout {
  from {
    right: 10px;
  }
  to {
    right: -520px;
  }
}
@keyframes go {
  0% {
    left: 0;
  }
  50% {
    left: 10px;
  }
  100% {
    left: 0;
  }
}
